import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'voter',
    templateUrl: './voter.component.html'
})

export class VoterComponent {
    @Input() name: string;
    @Output() voted = new EventEmitter<boolean>();
    didVote = false;

    vote(agreed: boolean) {
        this.voted.emit(agreed);
        this.didVote = true;
    }
}
