import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AppConfig } from '../../config/app-config'

@Component({
    selector: 'app-nav',
    templateUrl: './nav.component.html',
    styleUrls: ['./nav.component.css']
})

export class AppNavComponent implements OnInit {
    menuItems: any[];

    constructor(private translateService: TranslateService) {
        translateService.setDefaultLang('en');
        translateService.use('en');
    }
    ngOnInit(){
        this.loadMenus();
    }

    changeLanguage(language: string): void {
        this.translateService.use(language).subscribe(() => {
            this.loadMenus();
        });
    }

    private loadMenus(): void {
        this.translateService.get(['home', 'userControl', 'tableData','highChartText','dragDrop','reactJs','gulp']).subscribe((texts: any) => {
            this.menuItems = [
                { link: '/', name: texts['home'] },
                { link: '/' + AppConfig.routes.userControl, name: texts['userControl'] },
                { link: '/' + AppConfig.routes.tableData, name: texts['tableData'] },
                { link: '/' + AppConfig.routes.highChart, name: texts['highChartText'] },
                { link: '/' + AppConfig.routes.dragDrop, name: texts['dragDrop'] },
                { link: '/' + AppConfig.routes.reactJs, name: texts['reactJs'] },
                { link: '/' + AppConfig.routes.gulp, name: texts['gulp'] }
            ];
        });
    }
}