import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

// Import Component
import { Error404Component } from './core/error404/error404.component';
import { AppConfig } from './config/app-config';
import { ChartComponent } from './high-chart/high-chart.component';
import { UserControlComponent } from './user-control/user-control.component';
import { TableDataComponent } from './table-data/table-data.component';
import { KnowledgeTabComponent } from './knowledge-storage/knowledge-storage.component';
import { DragDropComponent } from './drag-drop/drag-drop.component';
import { ReactJSIntroduceComponent } from './react-js-intro/react-js-intro.component';
import { GulpComponent } from './gulp/gulp.component';

const routes: Routes = [
    { path: '', component: KnowledgeTabComponent },
    { path: AppConfig.routes.error404, component: Error404Component },
    { path: AppConfig.routes.home, component: Error404Component },
    { path: AppConfig.routes.highChart, component: ChartComponent },
    { path: AppConfig.routes.userControl, component: UserControlComponent },
    { path: AppConfig.routes.tableData, component: TableDataComponent },
    { path: AppConfig.routes.dragDrop, component: DragDropComponent },
    { path: AppConfig.routes.reactJs, component: ReactJSIntroduceComponent },
    { path: AppConfig.routes.gulp, component: GulpComponent },
    { path: '**', component: Error404Component }
]

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})

export class AppRoutingModule {

}